.sidebar{
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color:#f0f4f9 ;
  padding: 25px 15px;
}
.sidebar img{
  width: 20px;
}
.sidebar .menu{
  margin-left: 10px;
  display: block;
  cursor: pointer;
}
.sidebar .new-chat{
  margin-top: 50px;
  display:inline-flex ;
  align-items: center;
  gap: 10px;
  padding: 10px 15px;
  background-color: #e6eaf1;
  border-radius: 50px;
  font-size: 14px;
  color: gray;
  cursor: pointer;
}
.sidebar .recent{
  display: flex;
  flex-direction: column;
  animation:fadeIn 3s;
}
.sidebar .recent-title{
  margin-top: 30px;
  margin-bottom: 20px;
}
.sidebar .recent-entry{
  display: flex;
  align-items: start;
  gap: 10px;
  padding: 10px;
  padding-right: 40px;
  border-radius: 50px;
  color: #282828;
  cursor: pointer;
}
.sidebar .recent-entry:hover{

  background-color: #e2e6eb;
}
.sidebar .bottom{
  display: flex;
  flex-direction: column;
  
}
.sidebar .bottom-item{
  padding-right: 10px;
  cursor: pointer;
}

@media (max-width:600px) {
  .sidebar{
    display: none;
  }
  
}